import { useEffect, useState } from 'react';
import { FinancialApi } from 'utils/Api';
import { Table } from 'antd';
const { Column } = Table;

const Financial = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    FinancialApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          id: item._id,
          _id: item._id.slice(0, 7),
          createdAt: item.createdAt
            .replace(/[TZ]/g, ' ')
            .substr(0, item.createdAt.length - 5),
        }))
      );
    });
  }, []);

  return (
    <Table dataSource={tableData} rowKey="id">
      <Column
        title="会员编号"
        render={(_, record) => <p>{record.user.phone}</p>}
      />
      <Column title="资金变动" dataIndex="amount" />
      <Column title="余额" dataIndex="blance" />
      <Column title="时间" dataIndex="createdAt" />
      <Column title="描述" dataIndex="description" />
      <Column title="描述" render={(_, record) => record.status? '已完成': '处理中'}/>
    </Table>
  );
};

export default Financial;
